import React, { Component } from 'react'

import { Carousel, Icon } from 'antd'
import 'antd/lib/carousel/style/index.css'
import '../main/PageDisplay.css'
import { Img } from '../../ImgVid'

export default class PageDisplay extends Component {
  constructor(props) {
    super(props);
    this.next = this.next.bind(this);
    this.previous = this.previous.bind(this);
    this.carousel = React.createRef();
  }
  next() {
    this.carousel.next();
  }
  previous() {
    this.carousel.prev();
  }

  render() {
    const props = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1
    };
    return (
      <div className="portfolio-display">
        <Icon className="prev-button" type="caret-left" onClick={this.previous} />
        <Carousel autoplay autoplaySpeed={4000} ref={node => (this.carousel = node)} {...props}>
          <Img src='0901.png' />
          {/*<Img src='0902.png' />*/}
          <Img src='0903.png' />
          <Img src='0904.png' />
          <Img src='0905.png' />
          <Img src='0906.png' />
          {/*<Img src='0907.png' />*/}
          <Img src='0908.png' />
          <Img src='0909.png' />
          <Img src='0910.png' />
          <Img src='0911.png' />
          <Img src='0912.png' />
          <Img src='0913.png' />
          <Img src='0914.png' />
          <Img src='0915.png' />
          <Img src='0916.png' />
          <Img src='0917.png' />
          <Img src='0918.png' />
          <Img src='0919.png' />
          <Img src='0920.png' />
          <Img src='0921.png' />
          <Img src='0922.png' />
          <Img src='0923.png' />
          <Img src='0924.png' />
          <Img src='0925.png' />
          <Img src='0926.png' />
        </Carousel>
        <Icon className="next-button" type="caret-right" onClick={this.next} />
      </div>
    )
  }
}