import React, { Component } from 'react'

import LogoGather from './LogoGather'
import TextShuffle from './TextShuffle'
import ChapterFlip from './ChapterFlip'
import ProjCard from './ProjCard'
import PageDisplay from './PageDisplay'

//import './Main.css'

export default class Main extends Component {
  render() {
    return (
      <div className="Main-all">
        <section className="Main-sectionI Main-section">
          <LogoGather />
          <TextShuffle />
        </section>
        <div id="featured"><ProjCard /></div>
        <div id="about"><ChapterFlip /></div>
        <div id="portfolio"><PageDisplay /></div>
      </div>
    );
  }
}

